import { ReactComponent as SettingIcon } from "../icons/setting.svg";
import { ReactComponent as AdditionsIcon } from "../icons/additions.svg";
import { ReactComponent as BookingIcon } from "../icons/booking.svg";
import { ReactComponent as LocationIcon } from "../icons/location.svg";
import { ReactComponent as ProfileIcon } from "../icons/profile.svg";
import { ReactComponent as UsersIcon } from "../icons/users.svg";
import { ReactComponent as MessageIcon } from "../icons/messages.svg";
import { useLocation, useNavigate } from "react-router-dom";
import SideBarNavItem from "./SideBarNavItem";

let data = [
  {
    id: "0",
    icon: <ProfileIcon className="w-[20px] h-[20px]" />,
    title: "Profile",
    to: "/Profile",
  },
  {
    id: "1",
    icon: <SettingIcon className="w-[20px] h-[20px]" />,
    title: "Setting",
    to: "/Setting",
  },
  {
    id: "2",
    icon: <BookingIcon className="w-[20px] h-[20px]" />,
    title: "Booking",
    to: "/Booking",
  },
  {
    id: "3",
    icon: <UsersIcon className="w-[20px] h-[20px]" />,
    title: "Trainers",
    to: "/Trainers",
  },
  {
    id: "4",
    icon: <MessageIcon className="w-[20px] h-[20px]" />,
    title: "Messsages",
    to: "/Messsages",
  },
  {
    id: "5",
    icon: <LocationIcon className="w-[20px] h-[20px]" />,
    title: "Location",
    to: "/Location",
  },
  {
    id: "6",
    icon: <AdditionsIcon className="w-[20px] h-[20px]" />,
    title: "Additions",
    to: "/Additions",
  },
];

const SideBarLeft = () => {
  const location = useLocation();
  const navigate = useNavigate();

  return (
    <div className="w-full flex flex-col">
      {data.map((item, idx) => {
        return <SideBarNavItem {...item} key={idx} />;
      })}
    </div>
  );
};

export default SideBarLeft;
