import { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";

const SideBarNavItem = ({ title, to, icon, id }) => {
  const location = useLocation();
  const navigate = useNavigate();
  const [isActive, setIsActive] = useState(false);

  useEffect(() => {
    setIsActive(location.pathname === to ? true : false);
  }, [location]);
  return (
    <abbr onClick={() => navigate(to)} key={id} title={title}>
      <div className={`relative flex items-center justify-center group p-2 py-4 hover:bg-five transition-all duration-700 cursor-pointer ${isActive? "bg-five" : ""}`}>
        <div className={`${isActive ? "fill-two" : "fill-grayColor"}`}>
          {icon}
        </div>
        <div className={`absolute transition-all duration-700 w-1 left-0 bg-two bottom-0 group-hover:h-full ${isActive? "h-full" : " h-0"}`} />
      </div>
    </abbr>
  );
};

export default SideBarNavItem;
